import VueRouter from 'vue-router';
import home from '../pages/Home.vue';
import goosip from '../pages/category/Gossip.vue';
import share from '../pages/category/Share.vue';
import works from '../pages/category/Works.vue';
import laboratory from '../pages/Laboratory.vue';
import message from '../pages/Message.vue';
import partner from '../pages/Partner.vue';
import about from '../pages/About.vue';
import articleDetail from '../components/article/articleDetail.vue';

var router=new VueRouter({
  routes:[
    {path:'/',redirect:'/home'},
    {path:'/home',component:home},
    {path:'/articleDetail/:id',component:articleDetail},
    {path:'/category/share',component:share},
    {path:'/category/works',component:works},
    {path:'/category/gossip',component:goosip},
    {path:'/laboratory',component:laboratory},
    {path:'/partner',component:partner},
    {path:'/message',component:message},
    {path:'/about',component:about}

  ],
  linkActiveClass:''
});
export default router;



