<template>
    <div class="showme-container">
        <div class="header-content">
            <div class="block">
                <el-avatar :size="300" :src="circleUrl"></el-avatar>
            </div>
            <div class="info">
                <h1 class="title">{{title.hello}}<strong>{{title.author}}</strong>.</h1>
                <p class="introduction">{{introduction}}</p>
                <button class="btn">{{btnValue}}</button>
            </div>

        </div>
    </div>
</template>

<script>
    export default {
        name: "showMe",
        data: function () {
            return {
                circleUrl: './static/img/avatar.jpg',
              //circleUrl: 'http://pic51.nipic.com/file/20141025/8649940_220505558734_2.jpg',
                title:{hello:'Hi. I\'m ',author:'Minions'},
                // introduction:'  And this is Miniport, a free, fully responsive HTML5 site template designed by AJ for HTML5 UP &\n' +
                //   '                    released under the CCA license.',
              // introduction:'很多时候觉得自己对待任何事都很执着，一旦定了目标，就会不达目标不罢休，永不退缩！ 面对爱情也是一样，当真正遇到自己喜欢的一个人，会不顾一切去追。',
              introduction:'Many times I feel that I am very persistent in everything. Once I set a goal, I will never stop reaching it and never shrink back! Facing love is the same. When you really meet someone you like, you will go after them regardless of everything.',
              btnValue:'Learn about what I do'
            }
        }
    }
</script>

<style lang="less" scoped>
    .showme-container {
        .header-content {
            height: 650px;
            background-color: #FFFFFF;
            display: flex;
            justify-content: center;
            align-items: center;

            .info {
                margin-left: 50px;
                width: 50%;

              .title {
                    color: #3e3e3e;
                    font-size: 3.25em;
                    letter-spacing: -0.025em;
                    font-weight: 300;
                    margin-bottom: 0.2em;
                }

              .introduction {
                    font-size: 1.5em;
                    line-height: 2em;
                    margin-bottom: 2em;
                    color: #A1A1A1;
                }

                .btn {
                    width: 50%;
                    height: 80px;
                    color: #fff !important;
                    text-decoration: none;
                    font-weight: 700;
                    border: 0;
                    outline: 0;
                    cursor: pointer;
                    border-radius: 8px;
                    text-shadow: -1px -1px 0.5px rgba(0, 0, 0, 0.5);
                    overflow: hidden;
                    box-shadow: inset 0px 0px 0px 1px rgba(0, 0, 0, 0.5), inset 0px 2px 1px 0px rgba(255, 255, 255, 0.75);
                    background-color: #43B3E0;
                    padding: 1em 2.35em 1em 2.35em;
                    max-width: 24em;
                    font-size: 1.5em;
                    letter-spacing: -0.025em;

                }
            }
        }
    }
</style>



