<style scoped lang="less">
  .message-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    .comment{
      width: 80%;;
      margin-top: 2%;
      background-color: #FFFFFF;
    }

  }
</style>



