<template>
    <div class="contact-container">
        <div class="img">
            <img :src="head_ava">
        </div>
        <p>{{saying}}</p>
        <div class="contact-row" v-for="item in contact" :key="item.row">
<!--            <div :title="col.title" v-for="col in item.col"><i :class="'iconfont'+' '+col.icon"></i></div>-->
          <el-tooltip  class="item" effect="dark" :content="col.title" placement="top" v-for="col in item.col" :key="col.title">
            <div @click="go(col.url)"><i :class="'iconfont'+' '+col.icon"></i></div>
          </el-tooltip>

        </div>


    </div>
</template>

<script>
    export default {
        name: "contact",
        data:function () {
          return{
            head_ava:'./static/img/2.jpg',
            saying:'Write the Code. Change the World.',
            contact:[
              {
                row:1,
                col:[
                  {url:'https://github.com/Minions-xhr',title:'github',icon:'icon-github'},
                  {url:'https://www.csdn.net/',title:'CSDN',icon:'icon-CN_csdnnet'},
                  {url:'https://gitee.com/xzs988',title:'码云',icon:'icon-git'}
                ]},
              {
                row:2,
                col:[
                  {url:'/static/img/WeChat.jpg',title:'微信',icon:'icon-weixin'},
                  {url:'http://wpa.qq.com/msgrd?v=3&uin=&site=qq&menu=yes',title:'QQ',icon:'icon-qq'},
                  {url:'https://weibo.com/',title:'新浪微博',icon:'icon-weibo'},
                  {url:'/',title:'更多',icon:'icon-gengduo'}
                ]}
            ]
          }

        },
      methods:{
          go(url){
            window.location.href=url;
          }
      }
    }
</script>

<style scoped lang="less">

    .contact-container{
      background-color: #FFFFFF;
        width: 100%;
        height: 400px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        padding: 0 2%;
        margin-bottom: 30px;

        p{
            margin: 10px 0px;
            font-size: 14px;
            font-weight: 700;
        }
        .img{
            height: 250px;
            img{
                width: 100%;
                height: 100%;
            }
        }
        .contact-row{
            div{
                width: 40px;
                height: 40px;
                background-color: #E5E5E5;
                border-radius: 50%;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                margin: 5px;
              cursor: pointer;
            }
            div:hover{
                background-color:rgb(244,105,44);
                .iconfont{
                    color: white;
                }
            }
            .iconfont{
                font-size: 20px;

            }
        }

    }
</style>



