<template>
    <div class="recently-container">
      <h1 class="title">{{title}}</h1>
      <p class="subtitle">{{subtitle}}</p>
      <div class="content">
        <div class="content-item" v-for="item in infos" :key="item.id">
            <div class="info-img"><img :src="item.img_url"></div>
            <h1 class="info-title">{{item.title}}</h1>
          <p class="info-desc">{{item.desc}}</p>
        </div>
      </div>
      <p class="doubt">{{doubt}}</p>
      <button class="btn">{{btn_value}}</button>
    </div>
</template>

<script>
    export default {
        name: "recently",
       data:function () {
        return{
          title:'Here\'s some stuff I made recently.',
          subtitle:'Odio turpis amet sed consequat eget posuere consequat.',
          infos:[
            {id:1,img_url:'./static/img/head.jpg',title:'Consequat lorem',
              desc:'Ornare nulla proin odio consequat sapien vestibulum ipsum primis sed amet consequat lorem dolore.'
            },
            {id:2,img_url:'./static/img/head.jpg',title:'Consequat lorem',
              desc:'Ornare nulla proin odio consequat sapien vestibulum ipsum primis sed amet consequat lorem dolore.'
            },
            {id:3,img_url:'./static/img/head.jpg',title:'Consequat lorem',
              desc:'Ornare nulla proin odio consequat sapien vestibulum ipsum primis sed amet consequat lorem dolore.'
            }
          ],
          doubt:'Lorem ipsum dolor sit sapien vestibulum ipsum primis?',
          btn_value:'Get in touch with me'
        }
      },
      methods:{
      }
    }
</script>

<style scoped lang="less">
  .recently-container{
    height: 780px;
    background-color: #F7F7F7;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .title{
      font-size: 2em;
      letter-spacing: -0.015em;
      font-weight: 700;
      color: #3e3e3e;
      margin: 0 0 0.75em 0;
    }
    .subtitle{
      font-size: 1.25em;
      color: #8E8E8E;

    }
    .content{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      margin: 1% 0;
      .content-item {
        border-radius: 1%;
        height: 300px;
        width: 20%;
        background: #FFFFFF;
        box-shadow: inset 0px 1px 5px 1px rgba(0, 0, 0, 0.05);
        padding: 3%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        text-shadow: 1px 1px 0px #fafafa;
        margin: 2% 2%;
        .info-img {
          width: 80%;
          margin-top: 6%;
          margin-bottom: 3%;
          img {
            width: 100%;
          }
        }

        .info-title {
          color: #3e3e3e;
          font-size: 1.5em;
          letter-spacing: -0.015em;
          font-weight: 700;
          margin-bottom: -3%;
        }
        .info-desc{
          display: block;
          margin-block-start: 1em;
          margin-block-end: 1em;
          margin-inline-start: 0px;
          margin-inline-end: 0px;
          font-size: 1.25em;
          color: #8E8E8E;
          letter-spacing: 0.01em;
        }
      }
    }
    .doubt{
      font-size: 1.25em;
      color: #8E8E8E;
    }
    .btn {
      margin-top: 3%;
      width: 50%;
      height: 80px;
      color: #fff !important;
      text-decoration: none;
      font-weight: 700;
      border: 0;
      outline: 0;
      cursor: pointer;
      border-radius: 8px;
      text-shadow: -1px -1px 0.5px rgba(0, 0, 0, 0.5);
      overflow: hidden;
      box-shadow: inset 0px 0px 0px 1px rgba(0, 0, 0, 0.5), inset 0px 2px 1px 0px rgba(255, 255, 255, 0.75);
      background-color: #43B3E0;
      padding: 1em 2.35em 1em 2.35em;
      max-width: 24em;
      font-size: 1.5em;
      letter-spacing: -0.025em;


    }
  }
</style>



