<template>
  <div class="comment-container">
    <div class="top">
      <h2>发表评论</h2>
      <div class="input-text">
          <textarea
            placeholder="说点什么呢"
            rows=10
            class="text"
            maxlength="64"
            v-model="comment_content">
          </textarea>
      </div>
      <el-button type="primary" style="width: 82%;margin-left: 1.85%;font-size: 20px" @click="sendComment">发送
      </el-button>
    </div>
    <div class="bottom">
      <h4 style="color: #8c939d;margin: 8% 0 2% 2%">活捉 {{comments.length}} 条</h4>
      <div v-for="item in comments" :key="item.commentId">
        <div class="comment-item">
          <div class="img">
            <img :src="item.fromImg">
          </div>
          <div class="info">
            <h5 class="info-user">{{item.fromName}}</h5>
            <p class="info-time">{{item.createTime}}</p>
            <p class="info-content">{{item.commentContent}}</p>

          </div>
        </div>
        <!--          传递子评论-->
        <replay style="margin-left: 11%" :replay="item.replays"></replay>
      </div>
      <el-button type="primary" v-if="hasMore" @click="getMore"
                 style="width: 100%;margin: 2% 0;font-size: 20px" plain>
        加载更多
      </el-button>
      <div style="width: 100%;margin: 2% 0;font-size: 14px;text-align: center;letter-spacing: 0.25em" v-if="!hasMore">
        我是有底线的
      </div>
    </div>
  </div>

</template>

<script>
  import replay from '../comment/replay.vue';

  export default {
    name: "comment",
    data: function () {
      return {
        comment_content: '',
        comments: [],
        hasMore: true,
        pageNum: 1,
        pageSize: 5
      }
    },
    created() {
      this.getCommentByArticleId(this.id, this.pageNum, this.pageSize);
    },
    props: ['id'],
    methods: {
      getCommentByArticleId(id, pageNum, pageSize) {
        this.axios.get('comment/getList/' + id + '?pageNum=' + pageNum + '&pageSize=' + pageSize).then((response) => {
          var newData = response.data.data;
          //判断有无数据
          if (newData.length > 0) {
            // console.log(newData);
            this.comments = this.comments.concat(newData);//数组合并
            this.hasMore = true;
          } else {
            this.hasMore = false;
          }
        }).catch((response) => {
          this.hasMore = false;
        })
      },
      getMore() {
        this.pageNum = this.pageNum + 1;
        this.getCommentByArticleId(this.id, this.pageNum, this.pageSize);
      },
      //重置初始值
      rest(){
        this.comments=[];
        this.hasMore=true;
        this.pageNum=1;
        this.pageSize=5;
      },
      sendComment() {
        var articleId = this.id;
        var fromId = 0;
        var toId = 0;
        var parentId = 0;
        var commentContent = this.comment_content;
        var that=this;
        if (commentContent.length < 1) {
          this.$message({
            type: 'error',
            message: '写点呗!'
          });
          return;
        }
        this.axios.post('comment/add/', {articleId, fromId, toId, parentId, commentContent}).then((response) => {
          if (response.data.code == 0) {
            this.$message({
              type: 'success',
              message: '评论成功!'
            });
            //评论完 回到第一页
            that.rest();
            that.getCommentByArticleId(that.id,that.pageNum,that.pageSize);
          }
          else{
            this.$message({
              type: 'error',
              message: '评论失败!'
            });
          }

        }).catch((response) => {
        })
      }
    },
    components: {
      replay
    }
  }
</script>

<style scoped lang="less">
  .comment-container {
    width: 100%;

    .top {
      margin-top: 2%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      .input-text {
        width: 80%;
        margin: 2% 0;

        .text {
          width: 100%;
          font-size: 18px;
          line-height: 1.5em;
          padding: 1%;
          border: 2px solid #b3d8ff;
          background-color: #F4F6F7;
          letter-spacing: 0.2em;
        }
      }
    }

    .bottom {
      .comment-item {
        display: flex;
        border-top: 1px solid #b3d8ff;
        border-bottom: 1px solid #E5EAED;
        padding: 2%;
        align-items: center;

        .img {
          width: 80px;
          height: 80px;
          margin-right: 1%;

          img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
          }
        }

        .info {

          .info-user {
            font-size: 18px;

          }

          .info-time {
            font-size: 14px;
            color: #aaa;
            margin: 3% 0;
          }

          .info-content {
            width: 100%;
            margin-top: 2%;
            font-size: 16px;
            word-break: break-all;
            line-height: 2em;
            letter-spacing: 0.1em;
          }
        }
      }
    }
  }
</style>



