<template>
    <div class="replay-content" v-if="replay.length>0">
        <div class="replay-item" v-for="replay in replay" :key="replay.commentId">
          <div class="img">
            <img :src="replay.fromImg">
          </div>
          <div class="info">
            <h5 class="info-user">{{replay.fromName}} 回复 {{replay.toName}}</h5>
            <p class="info-time">{{replay.createTime}}</p>
            <p class="info-content">{{replay.commentContent}}</p>
          </div>
        </div>

      </div>
</template>

<script>
    export default {
        name: "replay",
      props:['replay'],
      data:function () {
        return{
        }
      }
    }
</script>

<style scoped lang="less">
  .replay-content{
    width: 90%;
    .replay-item{
      width: 90%;
      display: flex;
      border-top: 1px solid #b3d8ff;
      border-bottom: 1px solid oldlace;
      padding: 2% 0%;
      align-items: center;
      .img{
        width: 80px;
        height: 80px;
        margin-right: 1%;
        border-radius: 50%;
        img{
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      .info{

        .info-user{
          font-size: 18px;

        }
        .info-time{
          font-size: 14px;
          color: #aaa;
          margin: 3% 0;
        }
        .info-content{
          margin-top: 2%;
          font-size: 16px;
          word-break: break-all;
          line-height: 2em;
          letter-spacing: 0.1em;
        }
      }
    }
  }

</style>



