<template>
    <div class="articleList-container">
        <div class="article-content">
            <div class="article-item" v-for="item in articleList" :key="item.articleId">
                  <span class="s-item tcommonBox">
                    <span class="s-round-date">
                        <span class="month">{{item.createTime | dateFormat('MM月')}}</span>
                        <span class="day">{{item.createTime | dateFormat('DD')}}</span>
                    </span>
                </span>
                <div class="top">
                    <h1 class="title">{{item.articleName}}</h1>
                    <p>
                        <i class="iconfont icon-wulumuqishigongandashujuguanlipingtai-ico-" ></i>
                        <span>{{item.articleAuthor}} 发表于</span>
                        <i class="iconfont icon-shijian" ></i>
                        <span>{{item.createTime | dateFormat}} •</span>
                        <i class="iconfont icon-Icon-guankanliang" ></i>
                        <span>{{item.articleViews}} 次围观 •</span>
                        <i class="iconfont icon-tubiaozhizuo-" ></i>
                        <span>活捉 {{item.articleComments}} 条 •</span>
                        <i class="iconfont icon-like-b" ></i>
                        <span>{{item.articleLikes}}点赞 • </span>
                        <i class="iconfont icon-shoucang" ></i>
                        <span>{{item.articleStars}}收藏</span>
                    </p>
                </div>
                <div class="ui label">
                    <a>{{item.articleTag}}</a>
                </div>
                <div class="bottom">
                    <p>{{item.articleSummary}}</p>
                    <div class="img">
                      <router-link :to="'/articleDetail/'+item.articleId" tag="img" :src="item.articleImg"></router-link>
                        <el-button type="primary" size="mini" @click="goDetail(item.articleId)">阅读全文>></el-button>
                    </div>

                </div>
            </div>
          <el-button type="primary"  v-if="hasMore" @click="getMore"
                     style="width: 100%;margin: 2% 0;font-size: 20px" plain>
            加载更多
          </el-button>
          <div style="width: 100%;margin: 2% 0;font-size: 14px;text-align: center;letter-spacing: 0.25em"  v-if="!hasMore">我是有底线的 </div>
        </div>
        <div class="right">
            <contact></contact>
            <contact></contact>
            <contact></contact>
        </div>
      <el-backtop :right="20">UP</el-backtop>
    </div>
</template>

<script>
    import contact from '../../components/contact/contact.vue';
    export default {
        name: "articleList",
        created(){
          this.getArticleList(this.pageNum,this.pageSize);
        },
        data:function(){
          return{
            articleList:[],
            hasMore:true,
            pageNum:1,
            pageSize:5
          }
        },
        components:{
            contact
        },
        methods:{
          goDetail(articleId){
            this.$router.push('/articleDetail/'+articleId);
          },
          getArticleList(pageNum,pageSize){
            this.axios.get('article/getList?pageNum='+pageNum+'&pageSize='+pageSize).then((response)=>{
              var newData =response.data.data;
              //判断有无数据
              if(newData.length>0){
                // console.log(newData);
                this.articleList=this.articleList.concat(newData);//数组合并
                this.hasMore=true;
              }
              else{
                this.hasMore=false;
              }
            }).catch((response)=>{
              this.hasMore=false;
            })
          },
          getMore(){
            this.pageNum=this.pageNum+1;
            this.getArticleList(this.pageNum,this.pageSize);
          }
        }
    }
</script>

<style scoped lang="less">

    .articleList-container{
        width: 100%;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        .article-content{
            width: 55%;
            .article-item{
                background-color: #FFFFFF;
                margin-top: 50px;
                position: relative;
                padding: 15px 0px;
                .s-round-date {
                    position: absolute;
                    top: -20px;
                    left: -35px;
                    height: 60px;
                    width: 70px;
                    padding-top: 10px;
                    border-radius: 100px;
                    color: #fff;
                    background: #97dffd;
                    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
                    span {
                        text-align: center;
                        display: block;
                    }
                    .day {
                        font-size: 30px;
                        font-weight: 700;
                    }
                }
                .top{
                    display: flex;
                    flex-direction: column;
                    flex-wrap: wrap;
                    align-items: center;
                  margin-bottom: -3%;
                    .title{
                        color: #444;
                        padding: 0 5%;
                    }
                    p{
                        margin-block-start: 0.83em;
                        margin-block-end: 0.83em;
                        margin-inline-start: 0px;
                        margin-inline-end: 0px;
                        line-height: 24px;
                        text-align: center;
                        color: #555;
                        font-size: 14px;
                    }
                }
                .bottom{

                    p{

                        line-height: 24px;
                        margin-bottom: 10px;
                        text-indent: 2em;
                        padding-left: 2%;
                    }
                    .img{
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        img{
                            width: 95%;
                            margin-bottom: 10px;
                          cursor: pointer;;
                        }
                    }
                }
                .ui.label {
                    border-color: #97dffd;
                    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
                    background-color: #97dffd;
                    margin: 5px 0 15px 2px;
                    font-size: 14px;
                    position: relative;
                    left: -18px;
                    border-radius: 0 4px 4px 0;
                    padding: 7px 11.2px 7px 32px;
                    display: inline-block;
                    color: #fff;
                }
                .ui.label::after {
                    position: absolute;
                    content: '';
                    top: 100%;
                    left: 0px;
                    border-top: 0 solid transparent;
                    border-right-width: 1em;
                    border-right-color: #48456D;
                    border-right-style: solid;
                    border-bottom: 1em solid transparent;
                    border-left: 0 solid transparent;
                    width: 0;
                    height: 0;
                }
            }

        }
        .right{
            margin-left: 3%;
            width: 25%;
            margin-top: 50px;
            display: flex;
            flex-direction: column;
        }
    }
</style>



