<template>
  <div class="all-container">
    <h1 class="title">{{title}}</h1>
    <p class="subtitle">{{subtitle}}</p>
    <div class="content">
      <div class="content-item" v-for="item in infos" :key="item.id">
        <div class="img"><img :src="item.img_url"></div>
       <div class="info">
         <h1 class="info-title">{{item.title}}</h1>
         <p class="info-desc">{{item.desc}}</p>
       </div>
     </div>

    </div>
    <p class="doubt">{{doubt}}</p>
    <button class="btn">{{btn_value}}</button>
    <el-backtop :right="20">UP</el-backtop>
  </div>
</template>

<script>
  export default {
    name: "all",
    data:function () {
      return{
        title:'Here\'s all the stuff I do.',
        subtitle:'Proin odio consequat sapien vestibulum consequat lorem dolore feugiat.',
        infos:[
          {id:1,img_url:'./static/img/head.jpg',title:'Magna feugiat',desc:'Ornare nulla proin odio consequat.'},
          {id:2,img_url:'./static/img/head.jpg',title:'Veroeros primis',desc:'Ornare nulla proin odio consequat.'},
          {id:3,img_url:'./static/img/head.jpg',title:'Lorem ipsum',desc:'Ornare nulla proin odio consequat.'},
          {id:4,img_url:'./static/img/head.jpg',title:'Tempus dolore',desc:'Ornare nulla proin odio consequat.'},
          {id:5,img_url:'./static/img/head.jpg',title:'Feugiat aliquam',desc:'Ornare nulla proin odio consequat.'},
          {id:6,img_url:'./static/img/head.jpg',title:'Sed amet ornare',desc:'Ornare nulla proin odio consequat.'},
          {id:7,img_url:'./static/img/head.jpg',title:'Magna feugiat',desc:'Ornare nulla proin odio consequat.'},
          {id:8,img_url:'./static/img/head.jpg',title:'Magna feugiat',desc:'Ornare nulla proin odio consequat.'}
        ],
        doubt:'Lorem ipsum dolor sit sapien vestibulum ipsum primis?',
        btn_value:'See some of my recent work'
      }
    }
  }
</script>

<style scoped lang="less">
  .all-container{
    margin-top: 30px;
    padding: 2% 0;
    width: 100%;
    background-color: #F0F0F0;
    border-top: 1px solid  darkgray;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    .title{
      font-size: 2em;
      letter-spacing: -0.015em;
      font-weight: 700;
      color: #3e3e3e;
      margin: 0 0 0.75em 0;
    }
    .subtitle{
      font-size: 1.25em;
      color: #8E8E8E;

    }
    .content{
      width: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content:space-between;
      align-items: center;
      margin: 1% 0;
      .content-item {
        border-radius: 1%;
        height: 400px;
        width: 29%;
        box-shadow: inset 0px 1px 5px 1px rgba(0, 0, 0, 0.5);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        text-shadow: 1px 1px 0px #fafafa;
        margin: 1% 2%;

        .img{
          width: 100%;
          height: 60%;
          img{
              width: 100%;
            height: 100%;
          }
        }
        .info{
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          text-align: center;
          width: 100%;
          height: 40%;
          background-color: #FFFFFF;
          .info-title {
            color: #3e3e3e;
            font-size: 1.5em;
            letter-spacing: -0.015em;
            font-weight: 700;
            margin-bottom: -2%;
          }
          .info-desc{
            display: block;
            margin-block-start: 1em;
            margin-block-end: 1em;
            margin-inline-start: 0px;
            margin-inline-end: 0px;
            font-size: 1.25em;
            color: #8E8E8E;
            letter-spacing: 0.01em;
          }
        }

      }

    }
    /*解决最后一行 少个数导致 两端对齐不好看问题*/
    .content:after {
      content: "";
      width:33%;
    }
    .doubt{
      font-size: 1.25em;
      color: #8E8E8E;
    }
    .btn {
      margin-top: 3%;
      width: 50%;
      height: 80px;
      color: #fff !important;
      text-decoration: none;
      font-weight: 700;
      border: 0;
      outline: 0;
      cursor: pointer;
      border-radius: 8px;
      text-shadow: -1px -1px 0.5px rgba(0, 0, 0, 0.5);
      overflow: hidden;
      box-shadow: inset 0px 0px 0px 1px rgba(0, 0, 0, 0.5), inset 0px 2px 1px 0px rgba(255, 255, 255, 0.75);
      background-color: #43B3E0;
      padding: 1em 2.35em 1em 2.35em;
      max-width: 24em;
      font-size: 1.5em;
      letter-spacing: -0.025em;


    }
  }
</style>



