<template>
  <div class="articleDetail-container">
    <div class="article-content">
      <div class="article-item">
                  <span class="s-item tcommonBox">
                    <span class="s-round-date">
                        <span class="month">{{article.createTime | dateFormat('MM月')}}</span>
                        <span class="day">{{article.createTime | dateFormat('DD')}}</span>
                    </span>
                </span>
        <div class="top">
          <h1 class="title">{{article.articleName}}</h1>
          <p>
            <i class="iconfont icon-wulumuqishigongandashujuguanlipingtai-ico-"></i>
            <span>{{article.articleAuthor}} 发表于</span>
            <i class="iconfont icon-shijian"></i>
            <span>{{article.createTime | dateFormat}} •</span>
            <i class="iconfont icon-Icon-guankanliang"></i>
            <span>{{article.articleViews}} 次围观 •</span>
            <i class="iconfont icon-tubiaozhizuo-"></i>
            <span>活捉 {{article.articleComments}} 条 •</span>
            <i class="iconfont icon-like-b"></i>
            <span>{{article.articleLikes}}点赞 • </span>
            <i class="iconfont icon-shoucang"></i>
            <span>{{article.articleStars}}收藏</span>
          </p>
        </div>
        <div class="ui label">
          <a>{{article.articleTag}}</a>
        </div>
        <div class="info-detaile" v-html="article.articleContent">

        </div>
      </div>
    </div>
    <div class="comment">
      <comment :id="articleId"></comment>
    </div>
    <el-backtop :right="20">UP</el-backtop>
  </div>
</template>

<script>
  import comment from '../../components/comment/comment.vue';

  export default {
    name: "articleDetail",
    data: function () {
      return {
        articleId:this.$route.params.id,
        article:{}
      }
    },
    created(){
      this.getArticleById(this.articleId);
    },
    components: {
      comment
    },
    methods:{
      getArticleById(id){
        this.axios.get('article/getDetail/'+id).then((response)=>{
          this.article=response.data.data;
          // console.log(this.article);
        }).catch((response)=>{
          console.log(response);
        })
      }
    }
  }
</script>

<style lang="less">

  .articleDetail-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    .article-content {
      width: 80%;

      .article-item {
        background-color: #FFFFFF;
        margin-top: 50px;
        position: relative;
        padding: 15px 0px;

        .s-round-date {
          position: absolute;
          top: -20px;
          left: -35px;
          height: 60px;
          width: 70px;
          padding-top: 10px;
          border-radius: 100px;
          color: #fff;
          background: #97dffd;
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);

          span {
            text-align: center;
            display: block;
          }

          .day {
            font-size: 30px;
            font-weight: 700;
          }
        }

        .top {
          display: flex;
          flex-direction: column;
          flex-wrap: wrap;
          align-items: center;
          margin-bottom: -3%;

          .title {
            color: #444;
            padding: 0 5%;
          }

          p {
            margin-block-start: 0.83em;
            margin-block-end: 0.83em;
            margin-inline-start: 0px;
            margin-inline-end: 0px;
            line-height: 24px;
            text-align: center;
            color: #555;
            font-size: 14px;
          }
        }

        .info-detaile {
          display: flex;
          flex-wrap: wrap;
          flex-direction: column;
          justify-content: space-between;
          align-items: center;
          /*text-align: center;*/
          padding: 0 5%;
          img {
            width: 100%;
            margin: 5px 0;
          }

          p, h1, h2, h3, h4, h5, div {
            margin: 2px 0;
          }

        }

        .ui.label {
          border-color: #97dffd;
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
          background-color: #97dffd;
          margin: 5px 0 15px 2px;
          font-size: 14px;
          position: relative;
          left: -18px;
          border-radius: 0 4px 4px 0;
          padding: 7px 11.2px 7px 32px;
          display: inline-block;
          color: #fff;
        }

        .ui.label::after {
          position: absolute;
          content: '';
          top: 100%;
          left: 0px;
          border-top: 0 solid transparent;
          border-right-width: 1em;
          border-right-color: #48456D;
          border-right-style: solid;
          border-bottom: 1em solid transparent;
          border-left: 0 solid transparent;
          width: 0;
          height: 0;
        }
      }

    }
    .comment{
      width: 80%;;
      margin-top: 2%;
      background-color: #FFFFFF;
    }

  }
</style>



